.body {
  height: 100vh;
  font-family: "Poppins";
  width: 100vw;
}

.login-container {
  display: flex;
  font-family: "Poppins";
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.login-box {
  background-color: #2e2e4fbd;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 15px #00000080;
  text-align: center;
  z-index: 2;
  position: relative;
}

.login-box h1 {
  scale: 1.1;
  font-size: 58px;
  margin-bottom: 24px;
}

.login-box p {
  font-size: 16px;
  margin-bottom: 20px;
}

.discord {
  color: #7289da;
  font-weight: bold;
}

.login-button {
  width: 571px;
  padding: 15px 30px;
  background-color: #7289da;
  border: none;
  color: #ffffff;
  font-size: 18px;
  cursor: pointer;
  font-family: "Poppins";
  border-radius: 5px;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.login-button i {
  scale: 1.1;
  margin-left: 560px;
}

.login-button:hover {
  background-color: #5a6fb4;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.support-text {
  margin-top: 20px;
  font-size: 14px;
  font-family: "Poppins";
  color: #b0b0b0;
}

.support-text a {
  color: #7289da;
  text-decoration: none;
}

.support-text a:hover {
  text-decoration: underline;
  cursor: pointer;
}

.NextString {
  position: absolute;
  margin-top: 1px;
  font-weight: bold;
  font-size: 18px;
}

.LunarLogo {
  position: relative;
  right: 20px;
  width: 40px;
}
