.logs-button {
  display: flex;
  align-items: center;
  font-size: 16px;
  margin-top: -20px;
  font-family: "Poppins";
  margin-left: 220px;
  background-color: rgba(110, 110, 110, 0.877);
  color: #ccc;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  position: absolute;
  transition: background-color 0.2s ease;
}

.logs-button:hover {
  background-color: rgba(82, 82, 82, 0.877);
}

.icon {
  font-size: 20px;
  margin-right: 6px;
  color: #f8bf2e;
}

.logs-text {
  font-weight: bold;
  color: #ccc;
  margin-right: 15px;
}
