.sidebar {
  width: 200px;
  background-color: #222222;
  color: #fff;
  height: 98vh;
  padding: 20px;
  margin-bottom: -20px;
  margin-right: 1100px;
  box-sizing: border-box;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  font-family: "Poppins";
}

.title {
  font-size: 24px;
  margin-bottom: 40px;
  font-weight: bold;
}

.menu-list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 20px;
}

.menu-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px;
  border-radius: 5px;
  font-family: "Poppins";
  transition: background-color 0.3s ease;
  cursor: pointer;
}

.menu-item.active {
  background-color: #303131;
}

.menu-item:hover {
  background-color: #3d3d3d;
}

i {
  margin-right: 15px;
  font-size: 18px;
}

.profile-section {
  display: flex;
  align-items: center;
  background-color: #313133;
  padding: 10px;
  width: 160px;
  border-radius: 10px;
  font-family: "Poppins";
  margin-left: -10px;
  margin-top: 340px;
}

.profile-image {
  width: 40px;
  height: 40px;
  border-radius: 20%;
  margin-right: 12px;
}

.profile-info {
  flex-grow: 1;
}

.profile-name {
  display: block;
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  margin-left: -11px;
}

.Settings i {
  position: absolute;
  cursor: pointer;
  scale: 1.1;
  z-index: 999;
  margin-top: -8px;
  margin-left: -16px;
  transition: color 0.3s ease;
}

.Settings i:hover {
  color: #b9b9b9e7;
}

.loading {
  position: absolute;
  color: #b9b9b900;
}
