.Friends-score {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(90deg, #636363, #444444);
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    width: 900px;
    font-family: "Poppins";
    top: 220px;
    position: absolute;
    margin-left: 220px;
  }
  
  .Friends-info {
    display: flex;
    font-family: "Poppins";
    align-items: center;
  }
  
  .rank {
    font-weight: bold;
    margin-right: 10px;
  }
  
  .username {
    font-size: 16px;
  }
  
  .stats {
    display: flex;
    align-items: center;
  }
  
  .stat {
    display: flex;
    align-items: center;
    margin-left: 20px;
  }
  
  .stat span {
    margin-right: 5px;
    font-size: 18px;
  }
  
  .Score i{
    background: linear-gradient(180deg, #ff1100, #ff6600);
    scale: 1.2;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }