.Download-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 150px;
  padding: 5px;
  position: absolute;
  margin-top: -550px;
  border-radius: 8px;
  font-family: "Poppins";
  margin-left: 250px;
  scale: 1.2;
  transition: background-color 0.2s ease;
}

.Download-container:hover {
  background-color: #aaaaaa4d;
  cursor: pointer;
}

.Download-box {
  width: 150px;
  background-color: rgba(24, 24, 24, 0.63);
  height: 220px;
  z-index: 999999;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  border: 2px dashed #777;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
}

.download-icon i {
  color: #747474;
  font-size: 48px;
  margin-left: 15px;
}

.Download-text {
  font-size: 14px;
  position: absolute;
  font-weight: bold;
  color: #ffffff;
  margin-top: 180px;
}

.Download-subtext {
  position: absolute;
  font-size: 10px;
  margin-top: 200px;
  color: #f3f3f3;
}
