.friends-count {
  display: flex;
  position: absolute;
  align-items: center;
  font-family: "Poppins";
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  font-size: 18px;
  background-color: #5f5f5fd0;
  margin-left: 1180px;
  top: 40px;
  padding: 10px 16px;
  border-radius: 20px;
}

.friends-count i {
  margin-right: 8px;
}

.friends-users {
  font-weight: bold;
}

.separator {
  font-weight: 400;
  color: #c2c2c2b6;
  margin: 0 4px;
}

.current-friends {
  font-weight: 500;
  color: #c2c2c2b6;
}

.Online-friends {
  font-weight: 500;
  color: #e0e0e0;
}
