.InDevelopment {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  font-family: "Poppins", sans-serif;
  z-index: 9999999;
}

.inDevelopment {
  background: linear-gradient(180deg, #ff3300, #ff8800);
  color: rgba(255, 255, 255, 0.753);
  border: none;
  position: absolute;
  border-radius: 14px;
  padding: 18px 10px;
  font-size: 16px;
  font-weight: bold;
  width: 160px;
  display: flex;
  align-items: center;
  margin-top: 26px;
  margin-left: -212px;
  margin-top: -238px;
  filter: drop-shadow(0px 0px 6px rgba(255, 136, 0, 0.5));
  animation: glow 1.5s infinite alternate;
}

@keyframes glow {
  0% {
    filter: drop-shadow(0px 0px 10px rgba(255, 136, 0, 0.3));
  }
  100% {
    filter: drop-shadow(0px 0px 20px rgba(255, 136, 0, 0.5));
  }
}

.inDevelopment i {
  margin-right: 6px;
}
