.download-path-container {
  background-color: #2a2a2ab9;
  border-radius: 8px;
  padding: 16px 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  width: 600px;
  position: absolute;
  margin-left: 220px;
  margin-top: -500px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  font-family: "Poppins";
}

.Download-Text {
  position: absolute;
  font-size: 18px;
  margin-top: -10px;
  margin-left: 50px;
  font-family: "Poppins";
}

.path-text {
  font-size: 14px;
  margin-left: 50px;
  color: #ccc;
  flex-grow: 1;
  font-family: "Poppins";
  margin-right: 10px;
  margin-top: 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.browse-button {
  background-color: #efefef;
  border: none;
  color: rgb(31, 31, 31);
  padding: 7px 24px;
  font-weight: bold;
  font-family: "Poppins";
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.browse-button:hover {
  background-color: #b4b4b4;
}
