.body {
  margin: 0;
  padding: 0;
  font-family: opensans, sans-serif;
  background-color: #160047;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: #f5f5f5;
  overflow: hidden;
}

.Container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
}

.Box {
  background-color: #2e2e4fbd;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 15px #00000080;
  text-align: center;
  z-index: 1;
  position: relative;
  width: 200px;
  height: 50px;
}

.Box h1 {
  font-size: 58px;
  margin-bottom: 24px;
}

.Box p {
  font-size: 17px;
  margin-bottom: 20px;
}

.Box .discord {
  color: #7289da;
  font-weight: bold;
}

.StringDisplay {
  position: relative;
  top: -40px;
  border-radius: 10px;
  width: 200px;
  height: 100px;
}

.StringToDisplay {
  position: relative;
  font-weight: bold;
  top: 15px;
}
