.newsContainer {
  display: flex;
  position: static;
  align-items: center;
  justify-content: center;
  flex: 1;
  min-height: 13rem;
  margin-top: -230px;
  margin-left: -160px;
}

.news {
  position: absolute;
  display: flex;
  flex-direction: column;
  max-width: 55%;
  max-height: 294px;
  overflow: hidden;
  margin-top: -60px;
  background-size: cover;
  border-radius: 10px;
  transition: opacity 1s ease-in-out;
  opacity: 1;
}

.news::after {
  z-index: 99;
  content: "";
  width: calc(100% - 0.5rem);
  height: calc(100% - 0.5rem);
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 0.35rem;
  border: 0.25rem solid #ffffff09;
}

.news img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0.35rem;
}

.news .info {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 1rem;
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0), rgba(59, 59, 59, 0.56));
}

.news .info h2 {
  font-size: 1.8rem;
  line-height: 2.2rem;
  font-weight: 700;
  font-family: "Poppins";
  color: #fff;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5);
  margin: 0 0 0.5rem 0;
  text-transform: uppercase;
}

.news .info p {
  font-size: 1rem;
  line-height: 1.5rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 500;
  color: #dcdcdc;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
}

.news .info .date {
  position: absolute;
  bottom: 0.5rem;
  right: 2.5rem;
  font-size: 0.8rem;
  color: #c2c2c2ce;
  font-family: "Poppins";
  text-shadow: 0 0 1rem #0000000e;
  font-weight: bolder;
  margin: 0;
  padding: 0;
}

.newsLabel {
  position: absolute;
  top: 10px;
  left: 10px;
  color: #ffffffa6;
  text-shadow: 0 0 1rem #0000000e;
  font-weight: bold;
  font-size: 1.3rem;
  z-index: 100;
  text-transform: uppercase;
  font-family: "Poppins";
}

.progress-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: rgba(255, 255, 255, 0.2);
}

.progress {
  height: 100%;
  background: rgba(255, 255, 255, 0.8);
  transition: width 0.1s linear;
}
