.body {
  height: 100vh;
  font-family: "Poppins";
  width: 100vw;
}

.offline-container {
  display: flex;
  font-family: "Poppins";
  justify-content: center;
  align-items: center;
  height: 100%;
  position: absolute;
  margin-top: -700px;
  width: 100%;
}

.offline-box {
  background-color: #2e2e4fbd;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 15px #00000080;
  text-align: center;
  z-index: 2;
  position: relative;
}

.offline-box h1 {
  font-size: 50px;
  font-family: "Poppins";
  margin-bottom: 24px;
}

.offline-box p {
  font-size: 16px;
  font-family: "Poppins";
  margin-bottom: 20px;
}

.offline-box .discord {
  font-family: "Poppins";
  color: #7289da;
  font-weight: bold;
}

.offline-button {
  width: 640px;
  padding: 15px 30px;
  background-color: #7289da;
  border: none;
  color: #ffffff;
  font-size: 18px;
  cursor: pointer;
  font-family: "Poppins";
  border-radius: 5px;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.offline-button i {
  margin-left: 620px;
  scale: 1.2;
}

.offline-button:hover {
  background-color: #5a6fb4;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.offline-support-text {
  margin-top: 20px;
  font-family: "Poppins";
  font-size: 14px;
  color: #b0b0b0;
}

.offline-support-text a {
  color: #7289da;
  text-decoration: none;
}

.offline-support-text a:hover {
  text-decoration: underline;
}

.outdated {
  color: #db3a3a;
  font-weight: bolder;
  text-decoration: underline;
}

.Update {
  position: absolute;
  margin-left: -10px;
  font-weight: bold;
  font-size: 20px;
}
