.Reload-button-container {
  display: flex;
  align-items: center;
  color: #ffffff;
  position: absolute;
  background-color: rgba(89, 190, 69, 0.945);
  font-size: 18px;
  cursor: pointer;
  width: 210px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  margin-left: 480px;
  border-radius: 8px;
  margin-top: -20px;
  height: 30px;
  transition: background-color 0.3s ease;
}

.Reload-button-container:hover {
  background-color: rgba(54, 121, 41, 0.945);
}

.Reload-text {
  margin-left: 16px;
  font-family: "Poppins";
  justify-content: center;
}

.Reload-icon {
  position: relative;
  font-size: 18px;
  margin-left: 6px;
}
