.logout-hud {
  background-color: #2c2c2e;
  padding: 20px;
  position: absolute;
  display: flex;
  align-items: center;
  border-radius: 10px;
  width: 980px;
  font-family: "Poppins";
  margin-top: -630px;
  margin-left: 220px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
}

.logout-content {
  display: flex;
  align-items: center;
}

.Profile {
  margin-right: 15px;
}

.Profile img {
  position: absolute;
  height: 140px;
  margin-left: -15px;
  width: auto;
  margin-top: -87px;
}

.text-content h1 {
  margin: 0;
  font-size: 24px;
  color: white;
  margin-left: 120px;
}

.text-content p {
  margin: 5px 0 0 0;
  margin-left: 120px;
  font-size: 16px;
  color: #cccccc;
}

.Bold {
  color: rgb(240, 240, 240);
  font-weight: bold;
}

.AccountID {
  position: absolute;
  font-size: 10px;
  margin-top: -70px;
  margin-left: 800px;
  color: #cccccc86;
}
