.folder-icon-container {
  width: 50px;
  height: 50px;
  background-color: #7575758f;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  border-radius: 8px;
  margin-top: 0px;
  margin-left: -10px;
  z-index: 99999;
}

.folder-icon {
  color: #ffffff;
  margin-left: 18px;
  font-size: 24px;
}
