.title {
  position: absolute;
  font-family: "Poppins";
  left: 10px;
  top: -3px;
}

.control-button {
  color: white;
  width: 45px;
  height: 30px;
  background: none;
  border: none;
  cursor: pointer;
  position: relative;
  -webkit-app-region: no-drag;
  font-size: 25px;
}

.control-button::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.control-button:hover {
  background-color: #ffffff1a;
}

.control-button.close:hover {
  background-color: #e81123;
}

.controls {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 30px;
  background-color: #1b1b1b;
  display: flex;
  justify-content: flex-end;
  -webkit-app-region: drag;
  z-index: 2;
}

.buildId {
  font-size: 10px;
  font-family: "Poppins";
  color: #b4b4b4;
}
