.imported-build-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 150px;
  padding: 5px;
  font-family: "Poppins";
  position: absolute;
  margin-top: -550px;
  border-radius: 8px;
  margin-left: 650px;
  scale: 1.2;
  transition: background-color 0.2s ease;
}

.imported-build-container:hover {
  background-color: #aaaaaa2c;
  z-index: 99999999;
}

.Imported-box {
  width: 150px;
  height: 220px;
  z-index: 999;
  display: flex;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}

.Build-text {
  font-size: 14px;
  position: absolute;
  font-weight: bold;
  color: #fffffff3;
  margin-left: -45px;
  margin-top: 180px;
  z-index: 9999;
}

.Build-subtext {
  position: absolute;
  font-size: 10px;
  margin-top: 200px;
  color: #ffffffe1;
  margin-left: -52px;
  z-index: 9999;
}

.Splash img {
  background: no-repeat center center/cover;
  object-fit: cover;
  width: 150px;
  z-index: 99;
  height: 227px;
  transition: filter 0.3s ease;
}

.imported-build-container:hover .Splash img {
  filter: blur(10px);
}

.delete-button {
  position: absolute;
  z-index: 99999;
  color: rgb(240, 80, 59);
  margin-top: 185px;
  margin-left: 120px;
  cursor: pointer;
  scale: 1.3;
}

.favicon-icon {
  position: absolute;
  z-index: 10000;
  scale: 2.2;
  font-size: 36px;
  color: rgba(255, 255, 255, 0.432);
  opacity: 0;
  transition: opacity 0.3s ease;
  margin-left: 30px;
  margin-top: -30px;
  cursor: pointer;
}

.imported-build-container:hover .favicon-icon {
  opacity: 1;
}

.Loading{
  position: absolute;
  z-index: 10001;
  margin-left: -16px;
  margin-top: -2px;
}