.featured-shop-wrapper {
  position: absolute;
  display: flex;
  align-items: center;
}

.featured-shop {
  position: absolute;
  display: flex;
  margin-left: 938px;
  flex-direction: column;
  padding: 0.5rem;
  padding-bottom: 0;
  min-width: 21rem;
  width: 18rem;
  min-height: 18rem;
  max-height: 18rem;
  overflow: hidden;
  border-radius: 10px;
  margin-top: -270px;
  font-family: "Poppins";
}

.featured-shop.legendary {
  background-image: linear-gradient(45deg, #bd720a 0%, #9a1d1b 100%);
}

.featured-shop.epic {
  background-image: linear-gradient(45deg, #7a23cd 0%, #213fb0 100%);
}

.featured-shop.rare {
  background-image: linear-gradient(45deg, #0567a1 0%, #4743c2 100%);
}

.featured-shop.uncommon {
  background-image: linear-gradient(45deg, #135e13 0%, #177272 90%);
}

.featured-shop .typeAndTime {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 0.5rem;
}

.featured-shop label.itemType {
  font-weight: 700;
  font-size: 0.8rem;
  opacity: 0.9;
}

.featured-shop .cosmetic {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.featured-shop .cosmetic h2.name {
  z-index: 1;
  font-weight: 700;
  font-size: 1.2rem;
  margin: 0;
}

.featured-shop .cosmetic small.description {
  z-index: 2;
  font-size: 0.8rem;
  opacity: 0.9;
}

.featured-shop .cosmetic img.image {
  position: absolute;
  top: 1.7rem;
  left: 50%;
  width: 70%;
  height: auto;
  transform: translateX(-50%);
  filter: brightness(0.9);
  object-position: center;
  object-fit: contain;
}

.featured-shop .cosmetic.glider img.image {
  top: 1rem;
  width: 100%;
}

.featured-shop .cosmetic.pickaxe img.image {
  top: 0.8rem;
  width: 90%;
}

.featured-shop .purchase strong {
  font-weight: 700;
  font-size: 14px;
  color: white;
}

.featured-shop .purchase span {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.2rem;
  font-size: 0.8rem;
  font-weight: 500;
}

.featured-shop .purchase span p {
  font-weight: bold;
}

.featured-shop .purchase .vbucker {
  max-width: 25px;
}

.selector {
  z-index: 99999;
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 0.25rem;
  gap: 0.25rem;
}

.selector .selector-item {
  all: unset;
  padding: 0.25rem;
  border-radius: 100%;
}

.featured-shop .purchase {
  position: absolute;
  bottom: 0.5rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 200px;
  justify-content: center;
  gap: 0.35rem;
  padding: 0.3rem 0.8rem;
  color: white;
  font-size: 0.8rem;
  font-weight: 500;
  border-radius: 12px;
  background: #18181844;
  transition: transform 50ms;
  backdrop-filter: blur(10px);
}

.featured-shop .progress-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: rgba(255, 255, 255, 0.2);
}

.featured-shop .progress {
  height: 100%;
  background: rgba(255, 255, 255, 0.8);
  transition: width 0.1s linear;
}

.shop-animate-event {
  position: absolute;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.featured-shop label.itemTime {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0.2rem 0.5rem;
  gap: 0.35rem;
  font-size: 0.8rem;
  min-width: max-content;
  border-radius: 12px;
  background-color: #2b2b2b88;
  color: white;
}

.featured-shop label.itemTime svg {
  min-width: 1rem;
  color: white;
}

.featured-shop .cosmetic small.descriptionSeason {
  z-index: 2;
  font-size: 0.6rem;
  opacity: 0.6;
}
