.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  backdrop-filter: blur(0.5px);
}

.modal-content {
  background-color: #1a1a1ace;
  padding: 20px;
  height: 90px;
  border-radius: 4px;
  width: 580px;
  text-align: center;
  color: white;
  backdrop-filter: blur(10px);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h2 {
  position: absolute;
  justify-content: center;
  font-size: 20px;
  margin-left: 70px;
}

.Small-h2 {
  font-size: 14px;
  position: absolute;
  margin-left: 70px;
  color: rgba(177, 177, 177, 0.815);
  margin-bottom: -30px;
}

.modal-body {
  position: relative;
  margin-top: 55px;
  font-size: 18px;
}

.modal-body p {
  margin: -8px;
}

.progress-bar-Modal {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  overflow: hidden;
  margin-top: 10px;
  width: 100%;
  height: 6px;
}

.progress-Modal {
  height: 100%;
  background: rgba(216, 216, 216, 0.8);
  transition: width 1s linear;
}

.header-image-container {
  position: absolute;
  width: 100%;
  display: flex;
  margin-left: -10px;
  margin-bottom: -20px;
  align-items: center;
}

.header-image {
  width: 60px;
  position: absolute;
  height: 60px;
  margin-top: 16px;
  border-radius: 4px;
}
