.Clear-button-container {
  display: flex;
  align-items: center;
  color: #ffffff;
  position: absolute;
  background-color: rgb(51, 121, 187);
  font-size: 18px;
  cursor: pointer;
  width: 260px;
  margin-left: 210px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  height: 30px;
  margin-top: -20px;
  transition: background-color 0.3s ease;
}

.Clear-button-container:hover {
  background-color: rgb(30, 86, 139);
}

.Clear-text {
  font-family: "Poppins";
  margin-left: 16px;
  justify-content: center;
}

.Clear-icon {
  position: absolute;
  font-size: 18px;
  margin-left: 225px;
}
