.BattlePass-card {
  background-image: linear-gradient(to right, #698180, #405e55);
  color: white;
  height: 60px;
  font-family: "Poppins";
  border-radius: 8px;
  width: 360px;
  margin-left: 930px;
  margin-top: -165px;
  text-align: center;
  position: absolute;
  display: flex;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
  flex-direction: column;
  align-items: center;
}

.BattlePass-card-Purchased {
  background-image: linear-gradient(to right, #c77e27, #993e19);
  color: white;
  height: 60px;
  font-family: "Poppins";
  border-radius: 8px;
  width: 360px;
  margin-left: 930px;
  margin-top: -165px;
  text-align: center;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.BattlePass-value {
  font-size: 24px;
  font-weight: bold;
  margin-right: 312px;
}

.BattlePass-label-Purchased {
  font-size: 16px;
  margin-top: -2px;
  color: #e9c217;
  font-weight: 400;
  margin-right: 212px;
}

.BattlePass-label {
  font-size: 16px;
  margin-top: -2px;
  color: #c4c4c4;
  margin-right: 215px;
}

.tier-image-Purchased {
  width: 70px;
  height: auto;
  position: absolute;
  margin-left: 288px;
  width: 67px;
}

.tier-image {
  width: 70px;
  height: auto;
  position: absolute;
  margin-left: 290px;
}
