.help-card {
  width: 140px;
  padding: 20px;
  bottom: 90px;
  border-radius: 10px;
  background: linear-gradient(to bottom, #54c5e1, #26a69a);
  color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  position: absolute;
  font-family: "Poppins";
  margin-left: -10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
}

.question-icon {
  position: absolute;
  top: -15px;
  left: 53%;
  transform: translateX(-50%);
  width: 30px;
  height: 30px;
  background-color: #5f5f5f65;
  color: #ffffff;
  border-radius: 20%;
  scale: 1.4;
  font-size: 19px;
  line-height: 30px;
  font-family: "Poppins";
  font-weight: bold;
}

.question-icon i {
  margin-left: 4px;
}

.content h2 {
  margin: 0 0 10px;
  font-size: 19px;
}

.content p {
  margin: 0 0 20px;
  font-size: 14px;
}

.discord-button {
  background-color: #ffffff62;
  color: #292929d0;
  border: none;
  padding: 6px 50px;
  border-radius: 5px;
  font-family: "Poppins";
  cursor: pointer;
  font-size: 15px;
  font-weight: bold;
  margin-left: -10px;
  transition: all 0.3s ease;
}

.discord-button:hover {
  background-color: #f0f0f0d8;
  color: #292929;
}
