.check-update-btn {
  background-color: #4c7c46b9;
  border-radius: 8px;
  padding: 20px 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  width: 630px;
  font-weight: bold;
  position: absolute;
  font-size: 16px;
  margin-left: 220px;
  margin-top: -405px;
  border: none;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  font-family: "Poppins";
  transition: background-color 0.3s ease;
}

.check-update-btn:hover {
  background-color: #5ab14cb9;
  cursor: pointer;
}

.check-update-btn i {
  margin-left: 8px;
  scale: 1.3;
}
