.logout-button-container {
  display: flex;
  align-items: center;
  color: #ffffff;
  position: absolute;
  background-color: rgba(59, 58, 58, 0.37);
  font-size: 20px;
  cursor: pointer;
  width: 150px;
  margin-left: 813px;
  margin-top: 10px;
  border-radius: 8px;
  font-family: "Poppins";
  height: 50px;
  transition: background-color 0.3s ease;
}

.logout-button-container:hover {
  background-color: rgba(65, 64, 64, 0.87);
}

.logout-text {
  font-family: "Poppins";
  margin-left: 22px;
  justify-content: center;
}

.logout-icon {
  position: relative;
  font-size: 18px;
  margin-left: 8px;
}
