.note-container {
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(135deg, #3886e0, #2b4c72fd);
  padding: 6px;
  border-radius: 8px;
  color: #ffffff;
  font-family: "Poppins", sans-serif;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 930px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  margin-left: 220px;
  margin-top: -655px;
}

.note-content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  flex-grow: 1;
}

.note-text {
  margin-right: 10px;
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  white-space: nowrap;
}

.note-info {
  margin-right: 10px;
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  white-space: nowrap;
}

.version-tag {
  background-color: #1e436eb4;
  padding: 5px 10px;
  border-radius: 4px;
  margin-right: 10px;
  font-size: 13px;
  font-weight: bold;
  color: #ffffff;
  white-space: nowrap;
}

.note-link {
  color: #ffffff;
  font-weight: 400;
  margin-right: 10px;
  font-family: "Poppins";
  font-size: 14px;
}

.download-button {
  background-color: #ffffff;
  color: #007cf0;
  border: none;
  border-radius: 8px;
  padding: 4px 16px;
  font-size: 14px;
  font-family: "Poppins";
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.download-button:hover {
  background-color: #e0e0e0;
}

.warning-sign {
  width: 35px;
  position: relative;
  top: 3px;
  scale: 1.2;
  margin-left: 4px;
}
